import { Box } from '@mui/material';
import DataIcon from './premium-icons/data.png';

export default function DataWifi({ sx, ...other }) {
  return (
    <Box
      component="img"
      src={DataIcon}
      sx={{
        width: 80,
        height: 80,
        objectFit: 'contain',
        mixBlendMode: 'multiply',
        ...sx
      }}
      {...other}
    />
  );
}
