import { Box } from '@mui/material';
import BonusIconImg from './premium-icons/bonus.png';

export default function MoneyCard({ sx, ...other }) {
  return (
    <Box
      component="img"
      src={BonusIconImg}
      sx={{
        width: 80,
        height: 80,
        objectFit: 'contain',
        mixBlendMode: 'multiply',
        ...sx
      }}
      {...other}
    />
  );
}
