import { Box } from '@mui/material';
import PrintingIconImg from './premium-icons/printing.png';

export default function RechargeCard({ sx, ...other }) {
  return (
    <Box
      component="img"
      src={PrintingIconImg}
      sx={{
        width: 80,
        height: 80,
        objectFit: 'contain',
        mixBlendMode: 'multiply',
        ...sx
      }}
      {...other}
    />
  );
}
