import { Box } from '@mui/material';
import ElectricIconImg from './premium-icons/electricity.png';

export default function Electricity({ sx, ...other }) {
  return (
    <Box
      component="img"
      src={ElectricIconImg}
      sx={{
        width: 80,
        height: 80,
        objectFit: 'contain',
        mixBlendMode: 'multiply',
        ...sx
      }}
      {...other}
    />
  );
}
