import { Box } from '@mui/material';
import ResultIconImg from './premium-icons/exam.png';

export default function ResultCheck({ sx, ...other }) {
  return (
    <Box
      component="img"
      src={ResultIconImg}
      sx={{
        width: 80,
        height: 80,
        objectFit: 'contain',
        mixBlendMode: 'multiply',
        ...sx
      }}
      {...other}
    />
  );
}
