import { Box } from '@mui/material';
import CashIcon from './premium-icons/cash.png';

export default function Airtime2Cash({ sx, ...other }) {
  return (
    <Box
      component="img"
      src={CashIcon}
      sx={{
        width: 80,
        height: 80,
        objectFit: 'contain',
        mixBlendMode: 'multiply',
        ...sx
      }}
      {...other}
    />
  );
}
