import { Box } from '@mui/material';
import AirtimeIconImg from './premium-icons/airtime.png';

export default function Airtime({ sx, ...other }) {
  return (
    <Box
      component="img"
      src={AirtimeIconImg}
      sx={{
        width: 80,
        height: 80,
        objectFit: 'contain',
        mixBlendMode: 'multiply',
        ...sx
      }}
      {...other}
    />
  );
}
