import { Box } from '@mui/material';
import CableIconImg from './premium-icons/cable.png';

export default function SmartTv({ sx, ...other }) {
  return (
    <Box
      component="img"
      src={CableIconImg}
      sx={{
        width: 80,
        height: 80,
        objectFit: 'contain',
        mixBlendMode: 'multiply',
        ...sx
      }}
      {...other}
    />
  );
}
