import { Box } from '@mui/material';
import SmsIconImg from './premium-icons/sms.png';

export default function News({ sx, ...other }) {
  return (
    <Box
      component="img"
      src={SmsIconImg}
      sx={{
        width: 80,
        height: 80,
        objectFit: 'contain',
        mixBlendMode: 'multiply',
        ...sx
      }}
      {...other}
    />
  );
}
